.circleRed {
    width: 20px;
    height: 20px;
    border: 1px solid #8F71B7;
    border-radius: 50%;
    background-color: #8F71B7;
    margin-left: 10px;
}

.circleGray {
    width: 20px;
    height: 20px;
    border: 1px solid #7f7d81;
    border-radius: 50%;
    background-color: #7f7d81;
    margin-left: 10px;
}

.blankDiv {
    height: 40vh;
    margin-right: 3vw;
    margin-top: 10vh
}

.imgStyle {
    position: relative !important;
    bottom: 0;
    right: 0;
    z-index: 1;
}

.headerNav {
    justify-content: flex-end !important;
    margin-right: 10vw;
}

.web3Button {
    margin-left: 2vw;
}

.headerBar {
    height: 20vh;
    width: 100vw;
    background-color: white
}

.mainContent {
    height: 80vh;
    width: 100vw;
    background-color: white
}

.mainBar {
    background-color: #8F71B7;
    margin-left: 0.5vw;
    margin-right: 0.5vw;
    border-radius: 5px;
}

.subNav {
    color: white;
}

.mainContainer {
    background-color: white;
}

.container {
    text-align: center;
}

.header {
    margin-bottom: 40px;
    /* display: flex; */
    /* flex-direction: row; */
    justify-content: center;
    align-items: center;
}

.card {
    display: flex;
    flex-direction: row;
}

.header>div {
    padding: 5px;
}

.condition {
    padding: 50px;
    max-width: 500vw;
    /* margin: 0 auto; */
    margin-left: 200vw;
    margin-right: 200vw;
}

.inputContainer {
    margin-top: 40px;
}

.actions {
    display: flex;
    flex-direction: row-reverse;
    /* justify-content: space-evenly; */
    margin: 0 auto;
    width: 300px;
}

h3 {
    margin-top: 40px;
}

.outcome {
    /* display: flex; */
    /* flex-direction: row; */
    border: 1px solid #ddd;
    margin-top: 20px;
    padding: 10px 20px;
    /* align-items: center; */
    border-radius: 4px;
    /* justify-content: center; */
}

.outcomeInfo {
    margin-left: 20px;
}

.rightOutcome {
    border: 1px solid green;
}

.wrongOutcome {
    border: 1px solid red;
}

.bold {
    font-weight: bold;
}

.bar {
    display: inline-block;
    margin-top: 15px;
}